.wishlist {
  padding: 20px;
  max-width: 1920px;
  margin: 0 auto;
}

.wishlist-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}

.wishlist-header h2 {
  font-size: 24px;
  color: #333;
  text-align: center;
  flex: 1;
  margin: 0;
}

.add-category-btn {
  padding: 10px 20px;
  background-color: #6495ed;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.2s;
  position: absolute;
  right: 0;
}

.add-category-btn:hover {
  background-color: #30518d;
}

.wishlist-products,
.category-products {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}

.wishlist-empty {
  text-align: center;
  padding: 50px 20px;
}

.wishlist-empty h2 {
  font-size: 24px;
  color: #333;
}

.wishlist-empty p {
  font-size: 16px;
  color: #666;
  margin-top: 10px;
}

.wishlist-error {
  text-align: center;
  padding: 50px 20px;
  font-size: 16px;
  color: #ff0000;
}

.category-sections {
  margin-top: 20px;
}

.category-section {
  margin-bottom: 10px;
}

.drop-container {
  padding: 20px 0;
}

.placeholder-card.drop-target {
  border: 2px dashed #6495ed;
}

.category-header {
  background-color: #fe7fbf;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  position: relative; /* Ensure buttons can be positioned absolutely within */
}

.category-header h3 {
  font-size: 22px;
  color: #333;
  margin: 0;
  flex: 1;
}

.category-header .category-edit-input {
  font-size: 22px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 50%; /* Reduced width to half */
  margin-right: 10px;
}

.category-buttons {
  position: absolute;
  right: 20px; /* Fix buttons to the right */
  display: flex;
  gap: 5px; /* Space between buttons */
}

.no-products-text {
  font-size: 16px;
  color: #666;
  margin: 10px 0 20px 0;
  grid-column: 1 / -1; /* Ensure it spans the grid */
}

.placeholder-card {
  width: 100%;
  height: 380px; /* Match product card height */
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.plus-icon {
  font-size: 48px;
  color: #6495ed;
}

.edit-category-btn,
.confirm-edit-btn,
.delete-category-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
}

.disappearing {
  display: none;
}

.edit-category-btn:hover svg,
.confirm-edit-btn:hover svg,
.delete-category-btn:hover svg {
  stroke: #6495ed;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeInOverlay 0.3s ease-in forwards;
}

@keyframes fadeInOverlay {
  to {
    opacity: 1;
  }
}

.modal,
.delete-modal,
.delete-with-products-modal {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  width: 400px;
  max-width: 90%;
  animation: fadeInModal 0.3s ease-in forwards;
}

@keyframes fadeInModal {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.modal label {
  display: block;
  font-size: 16px;
  color: #333;
  margin-bottom: 10px;
  width: 80%; /* Reduced label width */
  margin-left: auto;
  margin-right: auto;
}

.modal input {
  width: 80%; /* Match label width for consistent padding */
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.modal-error {
  font-size: 14px;
  color: #ff0000;
  margin-bottom: 10px;
  text-align: center;
}

.modal-buttons {
  display: flex;
  justify-content: center; /* Center buttons */
  gap: 10px;
}

.delete-modal .modal-buttons {
  justify-content: center; /* Center buttons in delete modal */
}

.delete-modal p,
.delete-with-products-modal p {
  font-size: 16px;
  color: #333;
  margin: 0 0 20px 0;
  text-align: center;
}

.add-btn,
.cancel-btn,
.delete-btn,
.confirm-delete-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.2s;
}

.add-btn {
  background-color: #28a745; /* Green color for Add button */
  color: white;
}

.add-btn:hover {
  background-color: #218838; /* Darker green on hover */
}

.confirm-delete-btn {
  background-color: #d32f2f; /* Red color for Yes button in delete modal */
  color: white;
}

.confirm-delete-btn:hover {
  background-color: #a62626; /* Darker red on hover */
}

.delete-btn {
  background-color: #d32f2f;
  color: white;
}

.delete-btn:hover {
  background-color: #a62626;
}

.cancel-btn {
  background-color: #d3d3d3;
  color: #333;
}

.cancel-btn:hover {
  background-color: #c0c0c0;
}